export let showToastError = (error, $buefy) => {

       $buefy.toast.open({
            duration: 3500,
            message: error,
            position: 'is-bottom',
            type: error
        })

}

export let showToastCustom = (msg, $buefy, typeClass='is-info') => {

    $buefy.toast.open({
         duration: 3500,
         message: msg,
         position: 'is-bottom',
         type: typeClass
     })

}  